.I {
  display: grid;
  grid-template-rows: 50px;
  align-items: center;
  margin-top: 20px;
  &I {
    height: 30px;
    padding: 10px;
    border-radius: 5px;
    border: solid 1px #ccc;
    box-shadow: 0 0 5px #ccc;
    font-size: 16px;
    color: #333;
    font-size: 30px;
  }
}
